.header {
  box-sizing: border-box;
  display: block;
  background-image: url("https://ratatum.com/wp-content/uploads/2018/03/1_jfR0trcAPT3udktrFkOebA-min.jpeg");
  background-color: rgba(0,0,0,.3);
  background-blend-mode: darken;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  align-items: center;
  width: 100%;
  height: 190px;
  padding: 20px;
}

h1 {
  font-size: 16px;
  color: #e8dfc9;
  margin: 20px 0 15px 0;
}

.text {
  background: bisque;
  height: 25px;
  width: 200px;
}

